<template>
  <div>
    <div class='left'>
      <el-card shadow="hover" class='card'>
        <div class='personal'>
          <div class='avatar'>
            <el-avatar shape="square" :size="80" :src="avatar"></el-avatar>
          </div>

          <div class='nickname'>{{nickname}}</div>

          <div class='name'>{{realname}} ({{school}})</div>

          <div class='auth' v-if='isAuth'>
            <el-image :src="require('@/../public/assets/auth.png')" style='width:30px'></el-image>
            <span>{{userType}}</span>
          </div>
        </div>

        <el-divider>
          <el-image :src="require('@/../public/assets/nav.png')" style='width:40px'></el-image>
        </el-divider>

        <div class='pages'>
          <div class='listItem' @click='routerChange("hollowList")'>
            <el-image ref='hollowIcon' :src='hollowIcon' style='width:40px'></el-image>
            <span>{{this.$t('Hollow.Title')}}</span>
          </div>
          <div class='listItem' @click='routerChange("marketList")'>
            <el-image ref='marketIcon' :src='marketIcon' style='width:40px'></el-image>
            <span>{{this.$t('Market.Title')}}</span>
          </div>
          <div class='listItem' @click='routerChange("newsList")'>
            <el-image ref='newsIcon' :src='newsIcon' style='width:40px'></el-image>
            <span>{{this.$t('News.Title')}}</span>
          </div>
          <div class='listItem' @click='routerChange("lafList")'>
            <el-image ref='lafIcon' :src='lafIcon' style='width:40px'></el-image>
            <span>{{this.$t('Laf.Title')}}</span>
          </div>
        </div>

        <el-divider></el-divider>

        <div class='user'>
          <div class='listItem' @click='routerChange("userHistory")'>
            <el-image :src="require('@/../public/assets/history.png')" style='width:40px'></el-image>
            <span>{{this.$t('User.History')}}</span>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../server.js'

export default {
  name: 'frame-left',
  data () {
    return {
      hollowIcon: require('@/../public/assets/hollow.png'),
      marketIcon: require('@/../public/assets/market.png'),
      newsIcon: require('@/../public/assets/news.png'),
      lafIcon: require('@/../public/assets/laf.png'),
      isAuth: false,
      nickname: '',
      realname: '',
      school: '',
      userType: '',
      avatar: '',
    }
  },
  methods: {
    routerChange (target) {
      this.$bus.$emit('routerChange', target)
    },
    routerIconChange (target) {
      this.hollowIcon = require('@/../public/assets/hollow.png');
      this.marketIcon = require('@/../public/assets/market.png');
      this.newsIcon = require('@/../public/assets/news.png');
      this.lafIcon = require('@/../public/assets/laf.png');

      if (target == 'hollowList') {
        this.hollowIcon = require('@/../public/assets/hollow_filled.png');
      }
      else if (target == 'marketList') {
        this.marketIcon = require('@/../public/assets/market_filled.png');
      }
      else if (target == 'newsList') {
        this.newsIcon = require('@/../public/assets/news_filled.png');
      }
      else if (target == 'lafList') {
        this.lafIcon = require('@/../public/assets/laf_filled.png');
      }
    },
    getUserInfo () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            this.nickname = response.data.username;
            this.realname = response.data.realname;
            this.school = this.$t("User.UserSchool" + response.data.school);
            this.userType = this.formatUserType(response.data.userstatus, response.data.authname);
            this.avatar = this.getUserAvatar(response.data.userid);
            if (response.data.userstatus >= 2) {
              this.isAuth = true;
            }
            else {
              this.isAuth = false;
            }
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    }
  },
  mounted () {
    this.getUserInfo();
    this.routerIconChange(this.$route.name);
    this.$bus.$emit('routerIconChangeFooter', this.$route.name)
    this.$bus.$on('routerIconChangeLeft', this.routerIconChange);
  },
  beforeDestroy () {
    this.$bus.$off('routerIconChangeLeft');
  }
}
</script>

<style scoped>
.card {
  border-radius: 20px;
  height: 800px;
}
.listItem {
  margin-top: 10px;
  cursor: pointer;
  line-height: 40px;
}
.listItem span {
  font-size: 25px;
  position: relative;
  top: -10px;
  margin-left: 5%;
}
.nickname {
  font-size: 30px;
  margin-top: 15px;
  margin-bottom: 15px;
}
.name {
  font-size: 20px;
  margin-bottom: 15px;
}
.auth span {
  position: relative;
  top: -9px;
}
.personal {
  margin-bottom: 50px;
}
</style>